import './magazines.scss';
import React from 'react'
import Page from 'src/layout/Page';
import SEO from 'components/seo';
import { Link } from 'gatsby';
import { Container, Row, Col, Card } from 'react-bootstrap';

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            path
            title
          }
        }
      }
    }
  }
`

const Magazines = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const magazineList = edges
    .filter(edge => !!edge.node.frontmatter.date)
    .map(edge => {
      const post = edge.node;
      return (
        <Col key={post.id} xs={12} sm={12} md={12} lg={12}>
          <Link to={post.frontmatter.path}>
            <Card className="contents-card">
              <Card.Body>
                <Card.Title>{post.frontmatter.title}</Card.Title>
                <Card.Subtitle>{post.frontmatter.date}</Card.Subtitle>
                <Card.Text>{post.excerpt}...</Card.Text>
              </Card.Body>
            </Card>
          </Link>
        </Col>
      );
    });
  return (
    <Page pageName="posts">
      <SEO title="Posts" />
      <Container>
        <h1>Magazines</h1>
        <p>루비콘의 에피소드가 담긴 포스팅입니다</p>
        <Row id="post-list">{magazineList}</Row>
      </Container>
    </Page>
  );
};
export default Magazines;